<template>
    <i-article>
        <article>
            <h1>JsonEditor</h1>
            <p>json</p>
            <inAnchor title="" h2></inAnchor>
            <Demo title="json-editor">
                <div slot="demo">
                    <div class="editor-container">
                      <json-editor ref="jsonEditor" v-model="value"/>
                    </div>
                </div>
                <div slot="desc">
                    <p>Json</p>
                </div>
                <i-code lang="html" slot="code">{{ code.default }}</i-code>
            </Demo>

            <div class="api">
                <inAnchor title="API" h2></inAnchor>
                <inAnchor title="JsonEditor props" h3></inAnchor>
                <table>
                    <thead>
                        <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>value</td>
                            <td>JSON</td>
                            <td>Object</td>
                            <td></td>
                        </tr>
                        <tr>
                            <td>theme</td>
                            <td></td>
                            <td>String</td>
                            <td>rubyblue</td>
                        </tr>
                        <tr>
                            <td>lint</td>
                            <td>JSON</td>
                            <td>Boolean</td>
                            <td>true</td>
                        </tr>
                    </tbody>
                </table>

                <inAnchor title="JsonEditor methods" h3></inAnchor>
                <table>
                    <thead>
                        <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>input</td>
                            <td></td>
                            <td></td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </article>
    </i-article>
</template>
<script>
    import iArticle from '../../components/article.vue';
    import iCode from 'iCode';
    import Demo from '../../components/demo.vue';
    import Code from '../../code/JsonEditor';
    import inAnchor from '../../components/anchor.vue';
    import JsonEditor from '../../components/mhc/JsonEditor.vue'

    const jsonData = '[{"items":[{"market_type":"forexdata","symbol":"XAUUSD"},{"market_type":"forexdata","symbol":"UKOIL"},{"market_type":"forexdata","symbol":"CORN"}],"name":""},{"items":[{"market_type":"forexdata","symbol":"XAUUSD"},{"market_type":"forexdata","symbol":"XAGUSD"},{"market_type":"forexdata","symbol":"AUTD"},{"market_type":"forexdata","symbol":"AGTD"}],"name":""},{"items":[{"market_type":"forexdata","symbol":"CORN"},{"market_type":"forexdata","symbol":"WHEAT"},{"market_type":"forexdata","symbol":"SOYBEAN"},{"market_type":"forexdata","symbol":"SUGAR"}],"name":""},{"items":[{"market_type":"forexdata","symbol":"UKOIL"},{"market_type":"forexdata","symbol":"USOIL"},{"market_type":"forexdata","symbol":"NGAS"}],"name":""}]'

    export default {
        components: {
            iArticle,
            iCode,
            Demo,
            inAnchor,
            JsonEditor
        },
        data () {
            return {
                code: Code,
                value: JSON.parse(jsonData)
            }
        },
        methods: {}
    }
</script>



